@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}
body {
  background: repeating-linear-gradient(
      -45deg,
      #e7e7e7,
      #f7f7f7 10px
  ) no-repeat;
  min-height: 100vh;
  min-width: 100vh;
}
